.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

img {
  max-height: 200px;
  max-width: 200px;
}

table {
  margin-left: auto;
  margin-right: auto;
}

table.Courses {
  border: 1px solid black;
}

td {
  border: 1px solid black;
}

div.Students {
  display: flex;
  text-align: center;
}

div.student {
  margin-left: auto;
  margin-right: auto;
  background-color: aqua;
  border: 1px solid black;
}

#testi {
  margin-right: 7px;
  margin-left: 7px;
}

img#cart {
  max-height: 75px;
  float: right;
  margin-right: 20px;
  margin-top: -110px;
}

div#tuotteet {
  text-align: center;
}

div#cart {
  text-align: end;
}